<template>
  <div class="exo translate">
    <v-form>

      <div class="text-h5 font-weight-bold mb-3">Traduis en [{{ exercice.options.to }}]</div>
      <div class="text-body-1 font-weight-bold mb-3">
        <v-icon class="primary--text" @click="speech" v-if="canSpeech">mdi-volume-high</v-icon>
        "{{ exercice.question }}"
      </div>

      <v-text-field
        label="réponse"
        v-model="answer"
        :success-messages="showCorrection && isValid() ? exercice.response : ''"
        :error-messages="showCorrection && !isValid() ? exercice.response : ''"
        :readonly="showCorrection"
      >
      </v-text-field>

      <v-btn color="primary" class="mt-4" @click="submit">
        {{ !this.showCorrection ? 'Valider' : 'Exercice suivant' }}
      </v-btn>

    </v-form>
  </div>
</template>

<script>
  import mixin from '@/mixins/exo'
  import { mapGetters } from 'vuex'

  export default {
    mixins: [mixin],
    methods: {
      isValid() {
        return this.answer && this.answer.trim().toUpperCase() === this.exercice.response.trim().toUpperCase()
      },
      speech() {
        speechSynthesis.cancel()
        let msg = new SpeechSynthesisUtterance()
        msg.voice = this.getVoice()
        msg.volume = 1 // From 0 to 1
        msg.rate = 0.7 // From 0.1 to 10
        msg.pitch = 1 // From 0 to 2
        msg.text = this.exercice.question
        msg.lang = msg.voice.lang.replace('_', '-')
        // console.log(msg)
        speechSynthesis.speak(msg)
      },
      getVoice() {
        const voices = this.getDeviceVoices();
        // console.log(voices)
        return voices.find(ssv => ssv.lang.substring(0, 2) === this.exercice.options.from && ssv.localService === true)
      },
      ...mapGetters(['getDeviceVoices']),
    },
    computed: {
      canSpeech() {
        const hasAPI = 'speechSynthesis' in window && 'SpeechSynthesisUtterance' in window
        return !!(hasAPI && this.getVoice())
      },
    }
  }
</script>
